/* Main */

.main {
    display: flex;
    justify-content: space-between;
    position: relative;
    height: 100vh;
    overflow: hidden;
}

.main img {
    width: 100%;
    height: 100%;
}

.stars {
    position: absolute;
    width: 40%;
    height: 50%;
    right: 0;
}

.stars img {
    width: 15%;
    height: 27%;
    position: absolute;
    animation: blink 2s infinite;
    animation-delay: 1s;
    animation-timing-function: ease-in;
}

.stars img:nth-child(2n+1) {
    width: 11%;
    height: 19%;
    animation: blink 4s infinite;
}

@keyframes blink {
    0% {opacity: 0;}
    50% {opacity: 1}
    100% {opacity: 0}
}

@keyframes show {
    0% {opacity: 0;}
    50% {opacity: 1}
    100% {opacity: 1}
}

.star1 {
    right: 0;
    top: 10%;
}
.star2 {
    left: -25%;
    top: 20%;
}
.star3 {
    
}
.star4 {
    bottom: 10%;
    right: 0;
}
.star5 {
    bottom: -70%;
    right: 0;
}

.star6 {
    left: 50%;
}
.star7 {
    bottom: 27%;
    left: 24%;
}
.star8 {
    bottom: 0;
}
.arrow {
    bottom: -98%;
    left: 40%;
}

.main svg {
    width: 40%;
}

.ballon {
    width: 25%;
    transform: translateY(10vh);
}


/* Greeting */

.greeting {
    display: flex;
    width: 100%;
    height: 100vh;
}

.photos {
    position: relative;
    width: 70%;
    background-image: url('./assets/bg-greeting.png');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.greeting__text {
    width: 30%;
    position: relative;
}
.greeting__text img{
    width: 15%;
    animation: blink 4s infinite;
    position: absolute;
    bottom: 5%;
    left: 75%;
}








/* RWD APP to mobile */

.rotateMobile {
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.89);
    justify-content: center;
    align-items: center;
    position: absolute;
    z-index: 100;
    display: none;
}

.rotateMobile p {
    font-size: 2em;
    color: #fff;
}

@media (max-width: 500px) {
    .rotateMobile {
        display: flex;
    }
}

.main, .greeting {
    animation: show 4s;
}

h1 {
    overflow: hidden;
    animation: typing 10s ease-in;
      font-family: 'Pangolin', cursive;
      font-size: 18px;
      color: rgb(54, 54, 54);
  }
  
  /* The typing effect */
  @keyframes typing {
    from { height: 0 }
    to { height: 100% }
  }


.photo_box, .photo_box2 {
    display: flex;
    width: 100%;
    height: 100vh;
    position: absolute;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: .6s;
}

.photo_box.active, .photo_box2.active {
    opacity: 1;
}


.photo_box img {
    width: 40%;
    height: 84%;
}
.photo_box2 img {
    width: 80%;
    height: 84%;
}

.right, .left {
    width: 5%;
    position: absolute;
    z-index: 1000;
}

.right {
    right: 4%;
    bottom: 50%;
}

.left {
    left: 4%;
    bottom: 50%;
    transform: rotate(180deg);
}

.cake {
    width: 100%;
    height: 100vh;
    background-image: url('./assets/cake-bg.jpg');
    background-position: top;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.cake img {
    transition: 1s;
    height: 100vh;
    transform: translate(-40%, 21%);
}